/* ------| Slider Home|----- */
.view-banner-nodes {
	ul {
		list-style: none;
		padding: 0px;
		margin: 0px;
		// border: 1px solid red;
		li.swiper-slide {
			overflow: hidden;
			display: block;
			height: auto;
			// border: 1px solid green;
			.views-slideshow-swiper-main-frame-row-item {
				display: grid;
				width: 100%;
				height: 100%;
				.views-field-views-conditional-1 {
					width: 100%;
					.img-desktop{
						display: none;
						@include respond-to(xl) {
							display: block;
						}
					}
					.img-mobile {
						display: block;
						@include respond-to(xl) {
							display: none;
						}
					}
					.img-content {
						.img-mobile {
							img {
							width: 100%;
							height: 100%;
							}
						}
					}
					//imagen slider destock
					.cl-slider-homes-right,
					.cl-slider-homes-center,
					.cl-slider-homes-left,
					 .cl-slider-homes-specials
					 .cl-slider-homes-layout-seven {
						height: 100%;
						.iframe-conten {
							display: block;
							overflow: hidden;
							height: 50%;
							@include respond-to(m) {
								height: 100%;
							}
							.youtube-container--responsive {
								padding-bottom: 47.25%;
								// padding-bottom: initial;
								height: 100%;
							}
						}
					}
					.cl-slider-homes-bottom {
						height: 100%;
						@include respond-to(m) {
							height: 79%;
						}
						.iframe-conten {
							display: block;
							overflow: hidden;
							height: 100%;
							.youtube-container--responsive {
								padding-bottom: initial;
								height: 100%;
							}
						}
					}
				}
			}
		}
	}
}
/* ------| Ubicado Leth |----- */
.view-banner-nodes {
	.slider-homes-left {
		background-color: rgba(0, 0, 0, 0.5);
		@extend %slider-homes-lef;
  	//Titulo
  	.title-slider-homes {
			@extend %title-slider-home;
  	}
  	//Sub titulo
  	.body-slider-homes {
			@extend %body-slider-home;
  	}
  	//Buttom
  	.link-slider-homes {
  		@extend %link-slider-home;
  	}
	}
}

/* ------| Ubicado Rigth |----- */
.view-banner-nodes {
	.slider-homes-right {
		background-color: rgba(0, 0, 0, 0.5);
		@extend %slider-homes-lef;
		@include respond-to(m) {
