    }
    .body-slider-homes {
    	@extend %body-slider-home;
    	@include respond-to(m) {
    		text-align: right;
    	}
    }
	}
}

/* ------| Ubicado Centro |----- */
.view-banner-nodes {
	.slider-homes-center {
		background-color: rgba(0, 0, 0, 0.5);
		@extend %slider-homes-lef;
		@include respond-to(m) {
