/* ------| Paguinador |----- */
.view-banner-nodes {
	.skin-default {
    position: relative;
    .views-slideshow-controls-bottom {
      //Flechas paguinador
      .views-slideshow-controls-text {
        //Button Izquierda
        a[rel='prev'] {
          display: none;
          width: 40px;
          height: 40px;
          position: absolute;
          left: 2%;
          top: 40%;
          font-size: 0;
          background: url('../img/Icon/arrow-slide-prev.png') no-repeat center;
					z-index: 1;
        }
        //Buttom Pause
        .views-slideshow-controls-text-pause {
          display: none;
        }
        //Button Deracha
        a[rel='next'] {
          display: none;
          width: 40px;
          height: 40px;
          position: absolute;
          top: 40%;
          left: inherit;
          right: 2%;
          font-size: 0;
          background: url('../img/Icon/arrow-slide-next.png') no-repeat center;
					z-index: 1;
        }
      }
    }
	}
	//Hover flechas paguinador
	&:hover {
    .views-slideshow-controls-bottom {
      .views-slideshow-controls-text {
        a[rel='prev'],
        a[rel='next']{
          display: block;
        }
      }
    }
	}
}

/* ------| Ubicado Abajo |----- */
.view-banner-nodes {
	//posicion contenido abajo
	.field-content {
		.slider-homes-bottom {
			background-color: #f4f1f1;
			border-bottom: 1px solid #ebba00;
			@include respond-to(m) {
				padding: 20px 30px;
			}
			.conten {
				padding: 20px 15px 39px;
				overflow: hidden;
				margin: 0 auto;
				@include respond-to(xs) {
					display: flex;
					align-items: center;
				}
				@include respond-to(m) {
					padding: 0 15px 35px;
					width: 750px;
				}
				@include respond-to(xl) {
					width: 970px;
				}
				// @include respond-to(xxxl) {
				// 	width: 1170px;
				// }
				//redes sociales
				.Redes-sociales {
					display: none;
				}
				//titulo
				.title-slider-homes {
					width: 100%;
					font-family: $latobold;
					@include font-size(l);
					@include margin(.417 0);
					text-transform: uppercase;
					color: #585863;
					min-height: 50px;
					line-height: 20.9px;
					padding-bottom: 10px;
					@include respond-to(xs) {
						float: left;
					}
					@include respond-to(xs) {
						width: 41.66666667%;
						text-align: right;
						padding-right: 25px;
						padding-bottom: 0;
					}
				}
				//sub titulo
				.body-slider-homes {
					font-family: $crimson-textregular;
					@include font-size(m);
					text-align: left;
					color: #585863;
					line-height: 19.8px;
					border-top: 1px solid #ebba00;
					position: relative;
					min-height: 50px;
					padding: 15px 0 25px;
					margin: 0;
					@include respond-to(xs) {
						float: left;
						padding: 0 15px 0 25px;
						width: 33.33333333%;
						border: 0;
						border-left: 1px solid #ebba00;
					}
				}
				//button
				.link-slider-homes {
					margin: 15px 0;
					@include respond-to(xs) {
					margin: 0;
					}
					a {
						max-width: 145px;
						display: block;
						text-decoration: none;
						text-align: center;
						@include margin(0 auto);
						border: 1px solid #484851;
						font-family: $latolight;
						@include font-size(m);
						color: #484851;
						padding: 10px 35px;
						@include respond-to(xs) {
							float: right;
							@include margin(.834 0 0 auto);
						}
						@include respond-to(m) {
							@include margin(.834 .834 0 auto);
						}
						&:hover {
							background: color('grey-dark');
							color: color('white');
						}
					}
				}
			}
		}
	}
}

/* ------| Ubicado Ninguno |----- */
.view-banner-nodes {
	.field-content {
		.slider-homes-empty {
			display: none;
		}
	}
}

/* ------| bullet (puntos) |----- */
.view-banner-nodes {
	.views-slideshow-controls-bottom {
		margin-top: 30px;
		width: 100%;
		// margin-left: 15px;
		// margin-right: 15px;
		.views-slideshow-pager-field-processed {
			display: flex;
			justify-content: center;
			overflow-x: scroll;
			margin: 0 auto;
			padding: 10px 0;
			/* width */
			&::-webkit-scrollbar {
				width: 1em;
			}
		//  /* Cuadro */
		//  &::-webkit-scrollbar-track {
		// 	box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
		// 		// background: #f1f1f1;
		// 	}
		/* scroll */
			&::-webkit-scrollbar-thumb {
				// border-radius: 10px;
				background: #888;
			}
			.views-slideshow-pager-field-item {
				margin: 0 5px 10px;
				// min-width: 140px;
				@include respond-to(m) {
					margin: 0 5px;
				}
				//imagen
				.views-content-field-image-banner,
				.views-field-field-video-banner {
					img {
						max-height: 78px;
						width: 140px;
					}
				}
				//numero
				.views-field-counter {
					display: none;
				}
			}
		}
	}
}


/* ------| Tags categorias  |----- */
.noticias {
	.field-name-field-categoria {
		display: flex;
    justify-content: center;
		align-items: center;
		margin: 20px 0;
		position: relative;
		&:before {
			content: '';
			display: block;
			position: absolute;
			top: 20px;
			border-top: 1px solid #9e9ca1;
			width: 90%;
			left: 0;
			right: 0;
			margin: 0 auto;
			z-index: -1;
		}
		.field-item {
			display: inline-flex;
			span {
				margin: 0 10px 0 0;
				a {
					color: #484851;
					text-decoration: none;
					font-family: $latobold;
					display: block;
					padding: 10px 20px;
					border: 1px solid #9e9ca1;
					font-size: 0.89rem;
					background-color: white;
				}
			}
		}

		.hierarchical-select-item-separator {
			display: none;
		}
	}
	.pane-node-title {
		h1 {
			font-family: $latoblack;
			@include font-size(ll);
			color: color('grey');
			text-align: center;
			text-transform: uppercase;
			font-weight: bold;
			// @include margin-bottom(1.7);
			@include respond-to(m) {
				@include font-size(xxxl);
			}
		}
	}
}