/* ------| slider alineacion center |----- */
.slider-homes-layout-three-title {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  // background-color: rgba(0,0,0,.5);
  div:nth-child(1) {
    font-family: $latoblack;
    @include font-size(xl);
    color: color('white');
    text-transform: uppercase;
    text-align: center;
    @include respond-to(xl) {
      @include font-size(xxxl);
    }
  }
  div:nth-child(2) {
      font-family: $latolight;
      @include font-size(l);
      color: color('white');
      text-align: center;
      padding: 10px 0;
  }
  div:nth-child(3) {
    font-family: $crimson-textregular;
    @include font-size(m);
    color: color('white');
    text-align: center;
    padding: 10px 0;
  }
}