/*
//------------------------------------------------------------------------------
// Multimedia
//------------------------------------------------------------------------------
*/

/* ------| Conten mini panel |----- */
.pane-home-noticias-componente-noticia  {
	.pane-content {
		.panel-col-first {
			width: 100%;
			@include respond-to(xl) {
				width: 50%;

			}
		}
		.panel-col-last {
			display: none;
			@include respond-to(xl) {
				display: block;
			}
		}
	}
}

/* ------| swiper vertical |----- */
.view-display-id-home_noticias_destacadas {
  display: none;
	@include respond-to(xl) {
		display: block;
	}
	margin: 0 auto;
	padding: 15px;
	position: relative;
  &::before {
    content: "";
    position: absolute;
    top: 0px;
    right: 0px;
    border: 1px solid color('orange-primary');
    width: 94%;
    height: 100%;
  }
	ul {
		-webkit-padding-start: 0px;
		height: 380px;
		li.swiper-slide {
	    list-style: none;
			//video
			.view-display-id-default {
				.views-field-field-video-banner {
					width: 100%;
					height: 100%;
					display: block;
				}
			}
			//img
			.views-row {
				overflow: hidden;
				.views-field-view {
					width: 40%;
					float: left;
					padding-right: 20px;
					img {
						width: 100%;
					}
				}
        //titulo
        .multimedia-titulo {
          margin: 0 0 10px;
          a {
            font-family: $crimson-textbold;
            @include type-layout(o, 1.16);
            color: color('black');
            &:hover {
              text-decoration-line: underline;
            }
          }
        }
        //body
        .multimedia-body {
          font-family: $crimson-textregular;
          @include font-size(s);
          line-height: 1.2;
          color: color('black');
          &:hover {
            text-decoration-line: underline;
          }
        }
			}
		}
	}
}

/* ------| Flechas paginador |----- */
.view-display-id-home_noticias_destacadas {
	.skin-default {
    position: relative;
    .views-slideshow-controls-bottom,
		.views-slideshow-controls-top {
			display: block;
      //Conten Flechas
      .views-slideshow-controls-text {
        display: inline-flex;
        //Button Izquierda
        a[rel='prev'] {
          font-size: 0;
          width: 40px;
          height: 40px;
          position: absolute;
          top: inherit;
          left: 28px;
          bottom: -28px;
          border: 1px solid color('orange-primary');
          background: url('../img/Icon/arrow_prev.svg') no-repeat center;
          background-size: 33px 33px;
          transform: rotate(-90deg);
          background-color: white;
          z-index: 2;
        }
        //Buttom Pause
        .views-slideshow-controls-text-pause {
          display: none;
        }
        //Button Deracha
        a[rel='next'] {
          font-size: 0;
          width: 40px;
          height: 40px;
          position: absolute;
          left: 74px;
          top: inherit;
          bottom: -28px;
          border: 1px solid color('orange-primary');
          background: url('../img/Icon/arrow_next.svg') no-repeat center;
          background-size: 33px 33px;
          background-color: white;
          transform: rotate(-90deg);
          z-index: 2;
        }
      }
    }
	}
}

/* ------| Multimedia swioe horizontal movil |----- */
.view-display-id-home_noticias_destacadas_mobile {
	@include respond-to(xl) {
		display: none;
	}
	.swiper-container-horizontal {
		ul {
      padding: 0px;
      margin: 0px;
      list-style: none;
			li {
				.views-field-view {
					img{
            width: 100%;
          }
          .youtube-container--responsive {
            padding-bottom: 63.25%;
          }
        }
        .box-content {
          padding: 25px 25px 0;
          //Fecha
          .views-field-created {
            margin: 15px 0;
            text-align: center;
            span {
              font-family: $latoregular;
              @include font-size(s);
              color: color('grey-dark');
            }
          }
          //title
          .multimedia-titulo {
            a {
              font-family: $crimson-textroman;
              @include type-layout(l, 1.16);
              color: color('green-Primary');
              margin-bottom: 14px;
            }
          }
          //body
          .multimedia-body {
            div {
              font-family: $latoregular;
              @include font-size(s);
              margin-top: 10px;
            }
          }
        }
			}
		}
	}
}

/* ------| Bules swper |----- */
.view-display-id-home_noticias_destacadas_mobile {
  margin: 15px 0 20px;
	.views-slideshow-pager-field-processed {
		padding: 25px 0;
    display: flex;
    justify-content: center;
  .views-slideshow-pager-field-item {
    padding: 0 5px;
    a {
      width: 15px;
      height: 15px;
      font-size: 0;
      display: block;
      text-decoration: none;
      border: 1px solid color('blue-light');
      border-radius: 10px;
      background-color: color('grey-secundary');
      padding: 0;
    }
    &.active {
      a {
        background-color: color('blue-light');
        border: 1px solid color('blue-light');
      }
    }
  }
		@include respond-to(xl) {
			display: none;
		}
	}
}

/* ------| Multimedia Reproduccion |----- */
.view-display-id-home_noticias_destacada {
  display: none;
  padding-left: 20px;
  @include respond-to(xl) {
    display: block;
  }
	//img
	.view-id-componente_noticias_item {
    img {
      width: 100%;
      height: 100%;
    }
  }
	//titulo
	.views-field-title {
		margin: 20px 0 10px;
		span {
			font-family: $crimson-textbold;
			@include type-layout(ll, 1.16);
			margin-bottom: 14px;
			font-weight: 400;
		}
	}
	//body
	.views-field-body {
    width: 80%;
    float: left;
		div p {
			font-family: $crimson-textregular;
			@include type-layout(o, 1.1);
		}
	}
	//button
	.views-field-view-node {
    width: 20%;
    float: left;
		@extend %link-slider-home;
	}
}

/*
//------------------------------------------------------------------------------
// home Noticias
//------------------------------------------------------------------------------
*/

/* ------| quicktabs Tabs |----- */
#quicktabs-quicktabs_home_noticias {
  ul.quicktabs-tabs {
    width: 100%;
    padding: 0;
    margin: 0;
    padding-bottom: 40px;
    list-style: none;
    @include respond-to(xl) {
      margin: 0 10px;
    }
    li {
      margin: 10px 0;
      display: block;
      @include respond-to(xs) {
        display: inline-block;
        margin: 0px;
      }
      a {
        font-family: $latobold;
        @include font-size(s);
        text-align: center;
        text-decoration: none;
        background-color: #f0eeee;
        padding: 15px 50px;
        display: block;
        cursor: pointer;
      }
      &.active {
        a {
          background-color: color('blue-light');
          color: color('white');
        }
      }
      &:hover {
        a {
          background-color: color('blue-light');
          color: color('white');
        }
      }
    }
  }
}

/* ------| cards recientes y más vistos |----- */
.view-display-id-home_noticias_recientes,
.view-display-id-home_noticias_vistas {
  display: block;
  overflow: hidden;
  .view-content {
    .views-row {
      .views-column {
        margin-bottom: 15px;
        //Img
        .views-field-view {
          @include respond-to(xs) {
            max-height: 150px;
          }
          .views-field {
            img{
              width: 100%;
            }
            .youtube-container--responsive {
              padding-bottom: 62%;
            }
          }
        }
        .box-content {
          position: relative;
          padding: 20px 15px;
          border-top: 3px solid #ff8827;
          background: #f4f1f1;
          @include respond-to(xs) {
            min-height: 360px;
          }
          //Fecha
          .views-field-created {
            font-family: $latoregular;
            @include type-layout(xs, 1.23);
            color: color('orange');
            padding: 5px 0px;
          }
          //Titulo
          .views-field-title {
            font-family: $crimson-semibold;
            @include type-layout(sl, .90);
            // color: #484851;
            margin: 15px 0 20px;
            @include respond-to(m) {
              @include font-size(ll);
            }
          }
          //Body
          .views-field-body {
            font-family: $crimson-textregular;
            @include font-size(m);
            margin-bottom: 16px;
            @include respond-to(xs) {
              height: 7pc;
            }
          }
          //Button
          .views-field-view-node {
            a {
              display: block;
              max-width: 180px;
              font-family: $latoregular;
              @include font-size(m);
              // color: #484851;
              text-decoration: none;
              text-align: center;
              border: 1px solid color('orange');
              padding: 10px 20px;
              max-width: 180px;
              margin: 0 auto;
              @include respond-to(xs) {
                position: absolute;
                bottom: 25px;
                right: 0;
                left: 0;
              }
              &:hover {
                background-color: color('orange');
                color: #ffffff;
              }
              @include respond-to(m) {
                right: 25px;
                left: inherit;
                max-width: 150px;
              }
            }
          }
        }
      }
      @media screen and (min-width: 555px) and (max-width: 767px) {
        .views-column {
          width: 50%;
          float: left;
        }
      }
    }
  }
}

/* ------| Paguinadores |----- */
.view-display-id-home_noticias_recientes,
.view-display-id-home_noticias_vistas  {
  .text-center {
    width: 100%;
    position: relative;
    overflow: hidden;
    ul.pagination {
      background: #fff;
      border-radius: 0;
      padding: 10px;
      li {
        color: #484851;
        border-radius: 0;
        background-color: #fff;
        display: block;
        float: left;
        margin: 0 5px;
        a {
          color: #484851;
          border: 1px solid #e55d12;
          font-family: Lato,sans-serif;
          border-radius: 0;
          background-color: #fff;
        }
        &.active {
          span {
            border-color: #e55d12;
            background-color: #e55d12;
            border-radius: initial;
            color: color('white');
            border: initial;
            z-index: 1;
          }
        }
        &.next,
        &.prev,
        &.pager-last,
        &.pager-first {
          display: none;
        }
      }
    }
  }
}

/* ------| Noticias body y fecha |----- */
.noticias {
  #date-noticia {
    padding-top: 40px;
    padding-bottom: 20px;
    @include respond-to(m) {
      width: 550px;
      margin: 0 auto;
    }
    .pane-content {
      font-family: $latobold;
      color: color('orange-primary');
      @include type-layout(xxs, 1.23);
    }
  }
  #body-contain {
    @include respond-to(m) {
      width: 550px;
      margin: 0 auto;
    }
  }
}
