.view-id-componente_layout_4 {
  // max-width: 1140px;
  .view-content {
    .views-column {
      height: 302px;
      display: block;
      overflow: hidden;
      transition: .4s;
      margin-bottom: 15px;
      .views-field-views-conditional {
        height: 100%;
        .caja-listado {
          position: relative;
          height: 100%;
          &::before {
            content: '';
            background-color: color('black');
            opacity: .5;
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            z-index: 0;
          }
          //imagen
          .container-img,
          .container-video {
            height: 100%;
            figure {
              height: 100%;
              img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                max-height: 100%;
                z-index: -1;
              }
            }
          }
          // */ ----| contenedor |----*/
          .container-title-desc {
            position: absolute;
            top: 0px;
            left: 0px;
            width: 100%;
            height: 100%;
            .caja-titulo {
              font-family: $latoblack;
              @include font-size(l);
              text-transform: uppercase;
              color: color('white');
              text-align: center;
              margin: 20px 0;
            }
            .caja-body {
              font-family: $crimson-textregular;
              @include type-layout(l, .96);
              font-size: 1rem;
              color: color('white');
              text-align: center;
              margin-bottom: 8px;
            }
            .caja-enlace {
              padding: 5px 0;
              @include respond-to(xl) {
                opacity: 0;
              }
              a {
                display: block;
                width: 100%;
                max-width: 180px;
                font-family: $latolight;
                font-size: 0.9rem;
                color: white;
                text-align: center;
                text-decoration: none;
                padding: 10px 20px;
                margin: 0 auto;
                border: 1px solid color('white');
                background-color: transparent;
                z-index: -1;
                &:hover {
                  background: #fff;
                  color: color('grey-dark');
                }
                @include respond-to(m) {
                  background-color: color('black');
                }
                @include respond-to(xl) {
                  bottom: 20px;
                  position: absolute;
                  left: 0;
                  right: 0;
                  transition: all ease .3s;
                }
              }
            }
          }
          @include respond-to(xl) {
            &:hover {
              .caja-enlace {
                opacity: 1;
                a {
                  bottom: 10px;
                  z-index: 2;
                }
              }
              &::after {
                content: '';
                border: 1px solid #fff;
                width: 90%;
                height: 85%;
                position: absolute;
                top: 7px;
                right: 0;
                left: 0;
                margin: 0 auto;
                z-index: 1;
              }
            }
          }
        }
      }
    }
  }
  @media screen and (min-width: 555px) and (max-width: 767px) {
    .views-column {
      width: 50%;
      float: left;
    }
  }
}

