/* ------| Flechas paginador |----- */
.view-display-id-noticias_destacadas {
	.skin-default {
		position: relative;
		.slideshow-controls-top,
		.views-slideshow-controls-bottom {
			display: block;
			//Conten Flechas
			.views-slideshow-controls-text {
				display: none;
				@include respond-to(xl) {
					display: block;
				}
				//Button Izquierda
				a[rel='prev'] {
					font-size: 0;
					width: 50px;
					height: 50px;
					position: absolute;
					top: 50%;
					border: 1px solid #777;
					background: url('../img/Icon/arrow_prev.svg') no-repeat center;
					background-size: 50px 50px;
					padding: 1px 7px 2px;
				}
				//Buttom Pause
				.views-slideshow-controls-text-pause {
					display: none;
				}
				//Button Deracha
				a[rel='next'] {
					font-size: 0;
					width: 50px;
					height: 50px;
					position: absolute;
					top: 50%;
					left: inherit;
					right: 0;
					border: 1px solid #777;
					background: url('../img/Icon/arrow_next.svg') no-repeat center;
					background-size: 50px 50px;
					padding: 1px 7px 2px;

				}
			}
		}
	}
}

/* ------| Puntos Slider |----- */
.view-display-id-noticias_destacadas {
	.views-slideshow-pager-field-processed {
		@include bullet(orange, white, orange, orange );
		@include respond-to(xl) {
			display: none;
		}
	}
}
