$font-crimsontext:  '../fonts/Crimsontext' !default;
$font-lato:         '../fonts/Lato' !default;

@font-face {
    font-family: 'Crimson_textbold';
    src: url('#{$font-crimsontext}/CrimsonText-Bold.ttf') format('truetype');
    // src: url('#{$font-crimsontext}/crimsontext-bold-webfont.woff2') format('woff2'),
    // url('#{$font-crimsontext}/crimsontext-bold-webfont.woff') format('woff');
    // font-weight: bold;
    // font-style: normal;
}

@font-face {
    font-family: 'Crimson_textbolditalic';
    src: url('#{$font-crimsontext}/CrimsonText-BoldItalic.ttf') format('truetype');
    // src: url('#{$font-crimsontext}/crimsontext-bold-webfont.woff2') format('woff2'),
    // url('#{$font-crimsontext}/crimsontext-bold-webfont.woff') format('woff');
    // font-weight: bold;
    // font-style: normal;
}

@font-face {
    font-family: 'Crimson_semibold';
